/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  padding: 1rem 3rem;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  margin: 1rem;
  display: block;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  padding: 1rem 3rem;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  margin: 1rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
}

/* UI */
.no-webp .inicio {
  background-image: url(../../build/img/header.jpg);
}

.webp .inicio {
  background-image: url(../../build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  max-width: 70rem;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  text-align: left;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: none;
  opacity: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.6rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0rem;
  }
}
.navegacion a:hover {
  color: #71B100;
}
.navegacion a img {
  margin-left: 3rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  transition: visibility ease-in-out 0.3s, opacity linear 0.5s;
}

.logo-header {
  border: 0.1rem solid #FFFFFF;
}

.mobile-menu {
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.dark-mode-button {
  width: 3rem;
  filter: invert(100%);
}

.footer {
  padding: 2rem 0;
  background-color: #333333;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}

.contenido-footer {
  padding-top: 2rem;
}

.copyright {
  color: #FFFFFF;
  text-align: center;
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(199.5, 199.5, 199.5);
}
.anuncio .contenido-anuncio {
  padding: 1rem;
}
.anuncio .contenido-anuncio p {
  font-size: 1.8;
}
.anuncio .contenido-anuncio .precio {
  color: #71B100;
  font-weight: bold;
  font-size: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}
.anuncio .iconos-caracteristicas {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  list-style: none;
  padding: 0;
}
.anuncio .iconos-caracteristicas li {
  flex: 1;
  display: flex;
  margin-right: 2rem;
}
.anuncio .iconos-caracteristicas li:first-of-type {
  margin-left: 2rem;
}
.anuncio .iconos-caracteristicas li img {
  flex: 0 0 2rem;
  margin-right: 1rem;
}
.anuncio .iconos-caracteristicas li p {
  font-weight: bold;
}

.ver-todas {
  padding: 3rem 0;
}
.ver-todas a {
  margin: 0;
}

.no-webp .seccion-contactanos {
  background-image: url(../img/encuentra.jpg);
}

.webp .seccion-contactanos {
  background-image: url(../img/encuentra.webp);
}

.seccion-contactanos {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
}
.seccion-contactanos h2 {
  font-weight: 900;
  color: #FFFFFF;
  font-size: 4rem;
}
.seccion-contactanos p {
  color: #FFFFFF;
  font-size: 1.8rem;
}

.boton-amarillo {
  display: inline-block;
}

@media (min-width: 768px) {
  .boton-amarillo {
    display: block;
  }
}
@media (min-width: 768px) {
  .Blog-Testimonios {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .blog article {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
  }
}
.blog article {
  margin-bottom: 3rem;
}
.blog article:last-of-type {
  margin-bottom: 0;
}
.blog .entrada-blog a {
  color: #000000;
  margin-top: 3rem;
  display: block;
}
@media (min-width: 768px) {
  .blog .entrada-blog a {
    margin-top: 0;
  }
}
.blog .texto-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.blog .texto-blog h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.blog .texto-blog span {
  color: #E08709;
}

.testimonio {
  background-color: #71B100;
  color: #FFFFFF;
  padding: 3rem;
  font-size: 2.4rem;
  border-radius: 3rem;
}
.testimonio blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonio blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -3rem;
}
.testimonio p {
  text-align: right;
  color: #FFFFFF;
  font-size: 1.8rem;
}

.dark-mode {
  background-color: #1a1a1a;
  color: #FFFFFF;
}
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode a,
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid rgb(25.5, 25.5, 25.5);
}
.dark-mode .anuncio h1,
.dark-mode .anuncio h2,
.dark-mode .anuncio h3 {
  color: #e1e1e1;
}
.dark-mode .anuncio .iconos-caracteristicas li img {
  filter: invert(100%);
}

/* INTERNAS */
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros {
  margin-bottom: 5rem;
}

.nosotros-texto h2 {
  font-weight: 900;
  font-size: 2rem;
  text-align: left;
}
@media (min-width: 768px) {
  .nosotros-texto h2 {
    margin-top: 0;
  }
}

.anuncios {
  margin-bottom: 3rem;
}
.anuncios:last-of-type {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .anuncios {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: 3rem;
    column-gap: 2rem;
  }
}
.formulario fieldset {
  border-radius: 1rem;
}
.formulario p {
  color: #333333;
  font-size: 1.4rem;
}
.formulario label {
  text-transform: uppercase;
  font-weight: bold;
}
.formulario input:not([type=submit]),
.formulario select,
.formulario textarea {
  display: block;
  padding: 1rem;
  width: 100%;
  border: 0.1rem solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 1.5rem;
}
.formulario textarea {
  height: 10rem;
}
.formulario .radio-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 30rem;
}
.formulario input[type=radio] {
  width: auto;
  margin: 0;
}