.no-webp .inicio{
    background-image: url(../../build/img/header.jpg);
}

.webp .inicio{
    background-image: url(../../build/img/header.webp);
}

.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;

        @include tablet{
            min-height: 70rem;
        }

        .contenido-header{
            
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1{
        max-width: 70rem;
        color: $blanco;
        font-weight: 700;
        line-height: 2;
        text-align: left;
    }

}